<template>
	<div class="sub-container form">
		<div class="page-title center">
			<h2>문의 작성</h2>
		</div>

		<SelectBox
			class="marB_20"
			:options="options"
			v-model="selected"
			placeholder="카테고리를 선택해주세요"
		/>
		<!-- <SelectBox
				class="marB_20"
				:options="options"
				v-model="selected"
				@input="alert(displayToKey($event))"
			/> -->
		<input
			type="text"
			class="textform marB_20"
			placeholder="제목을 입력해주세요"
			v-model="title"
		/>
		<CKEditor :contents="contents" class="marB_20" ref="editor" />
		<div class="multiple-images">
			<AddImageMultiple :refName="'image1'" @image1="setImage1" />
			<AddImageMultiple :refName="'image2'" @image2="setImage2" />
			<AddImageMultiple :refName="'image3'" @image3="setImage3" />
		</div>

		<div class="botton-box center">
			<button class="btn dark" @click="addQuestion">등록</button>
		</div>
	</div>
</template>

<script>
import SelectBox from '@/components/common/SelectBoxV2';
import { addQuestion } from '@/api/question';
import CKEditor from '@/components/common/CKEditor';
import AddImageMultiple from '@/components/common/AddImageMultiple';
import { mapGetters } from 'vuex';
import Store from '@/store';

export default {
	components: { AddImageMultiple, SelectBox, CKEditor },
	computed: {
		...mapGetters(['image1', 'image2', 'image3']),
	},
	created() {
		Store.commit('SET_IMAGE1', null);
		Store.commit('SET_IMAGE2', null);
		Store.commit('SET_IMAGE3', null);
	},
	data: () => ({
		options: [
			{ id: 'NONE', label: '카테고리를 선택하세요' },
			{ id: 'PR', label: '프로그램' },
			{ id: 'GD', label: '굿즈' },
			{ id: 'AC', label: '계정' },
			{ id: 'SH', label: '배송' },
			{ id: 'ER', label: '교환 및 환불' },
			{ id: 'ETC', label: '기타' },
		],
		selected: {},
		title: '',
		contents: '',
		errMsg: '',
	}),
	methods: {
		async addQuestion() {
			if (this.selected == 'NONE') {
				this.errMsg = '카테고리를 선택해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.title) {
				this.errMsg = '제목을 등록해주세요';
				alert(this.errMsg);
				return false;
			}
			this.contents = this.$refs.editor.getContents();
			let data = new FormData();
			data.append('title', this.title);
			data.append('categoryId', this.selected.id);
			data.append('contents', this.contents);
			if (this.image1) {
				data.append('file1', this.image1);
			}
			if (this.image2) {
				data.append('file2', this.image2);
			}
			if (this.image3) {
				data.append('file3', this.image3);
			}
			try {
				await addQuestion(data);
				alert('1:1 문의가 등록되었습니다');
				await this.initForm();
			} catch (e) {
				alert(e);
			} finally {
				history.back();
			}
		},
		initForm() {
			this.selected = '';
			this.title = '';
			this.contents = '';
			Store.commit('SET_IMAGE1', '');
			Store.commit('SET_IMAGE2', '');
			Store.commit('SET_IMAGE3', '');
		},
		setImage1(data) {
			Store.commit('SET_IMAGE1', data);
		},
		setImage2(data) {
			Store.commit('SET_IMAGE2', data);
		},
		setImage3(data) {
			Store.commit('SET_IMAGE3', data);
		},
	},
};
</script>

<style lang="scss" scoped>
.sub-container.form {
	width: 520px;

	.textform {
		border: 2px solid #ddd;
		width: 100%;
		padding-left: 1em;
		box-sizing: border-box;
		height: 40px;
	}

	.textarea {
		border: 2px solid #ddd;
		width: 100%;
		padding: 1em;
		box-sizing: border-box;
		height: 240px;
	}
	.botton-box {
		display: flex;
		justify-content: center;
	}

	.write-btn {
	}
	.multiple-images {
		display: flex;
	}
}

.marB_20 {
	margin-bottom: 20px;
}
</style>
