// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchAdminQuestions(page) {
	return instanceWithAuth.get(`api/admin/question/all/${page}`);
}

function fetchQuestionsTotalCount() {
	return instanceWithAuth.get(`api/user/question/totalCount`);
}

function fetchAdminQuestionDetail(id) {
	return instanceWithAuth.get(`api/admin/question/question/detail/${id}`);
}

function searchAdminQuestion(page, categoryId, keyword, type) {
	return instanceWithAuth.get(`api/admin/question/search/${page}`, {
		params: { categoryId: categoryId, keyword: keyword, type: type },
	});
}

function deleteAdminQuestion(data) {
	return instance.delete(`api/admin/question/delete`, { data });
}

function fetchQuestions(created_id, page) {
	return instanceWithAuth.get(`api/user/question/all/${created_id}/${page}`);
}

function fetchQuestionDetail(id) {
	return instanceWithAuth.get(`api/user/question/detail/${id}`);
}

function addQuestion(data) {
	return instanceWithAuth.post(`api/user/question/new`, data);
}

function updateQuestion(id, data) {
	return instanceWithAuth.put(`api/user/question/update/${id}`, data);
}

function deleteQuestion(id) {
	return instanceWithAuth.delete(`api/user/question/delete/${id}`);
}

function addQuestionComment(id, data) {
	return instance.post(`api/admin/question/newReply/${id}`, data);
}

function updateQuestionComment(id, data) {
	return instanceWithAuth.put(`api/admin/question/updateReply/${id}`, data);
}
function deleteQuestionComment(id) {
	return instance.delete(`api/admin/question/deleteReply/${id}`);
}

export {
	fetchAdminQuestions,
	fetchQuestionsTotalCount,
	fetchAdminQuestionDetail,
	searchAdminQuestion,
	deleteAdminQuestion,
	fetchQuestions,
	fetchQuestionDetail,
	addQuestion,
	updateQuestion,
	deleteQuestion,
	addQuestionComment,
	updateQuestionComment,
	deleteQuestionComment,
};
